@include body-class(true) {

	.sui-sidenav {

		&.fui-sidenav {

			ul.sui-vertical-tabs {
				margin-top: 0;

				li.sui-vertical-tab {
					padding: 0;

					a {
						padding: 5px 15px;
						border-radius: 20px;

						&.current {
							background-color: palette(silver, soft);
							color: palette(gray, dark);
						}
					}
				}
			}
		}
	}
}