@include body-class(true) {

	.sui-dialog {

		// FIX: Center align loader
		.fui-loading-dialog {
			text-align: center;
		}

		// DIALOG: Publish
		&.fui-dialog-publish {

			.sui-dialog-content .sui-box {

				.sui-box-header {
					padding-top: #{$sui-gutter-md * 2};

					[class*="sui-icon"] {
						font-size: 20px;

						&:before {
							display: block;
						}

						+ .sui-box-title {
							margin-top: $sui-gutter-md;

							@include media(min-width, md) {
								margin-top: $sui-gutter;
							}
						}
					}

					@include media(min-width, md) {
						padding-top: #{$sui-gutter * 2};
					}
				}

				.sui-box-body {

					+ .sui-image {
						margin-top: #{$sui-gutter-md / 2};

						@include media(min-width, md) {
							margin-top: #{$sui-gutter / 2};
						}
					}
				}
			}
		}
		&.forminator-create-quiz-pagination-step {
			.sui-dialog-content {
				max-width: 560px;
			}
		}

		// DIALOG: Create Quiz âÂ Step #2.
		&.forminator-create-quiz-second-step {

			.sui-dialog-content {
				max-width: #{500 + $sui-gutter-md};

				.sui-box {

					.sui-box-body {

						.sui-toggle {

							&.fui-highlighted-toggle {
								margin-right: -#{$sui-gutter-md};
								margin-left: -#{$sui-gutter-md};

								@include media(min-width, md) {
									margin-right: -#{$sui-gutter};
									margin-left: -#{$sui-gutter};
								}
							}
						}
					}
				}

				@include media(min-width, md) {
					max-width: #{500 + $sui-gutter};
				}
			}
		}

		.forminator-question--description ul,
		.forminator-quiz--description ul {
			list-style-type: disc;
			padding-left: 2em;
		}
	}
}