// @todo remove when we upgrade to SUI 2.7.0

@include body-class(true) {

	.sui-box-selector {

		input {

			~ span {
				padding-right: #{($box-selector--padding * 3) - 1px};
				padding-left: #{($box-selector--padding * 2) - 1px};
				border: 1px dashed transparent;
				border-radius: $border-radius;

				&:not(:last-child) {
					border-bottom-color: transparent !important;
					border-bottom-left-radius: 0;
					border-bottom-right-radius: 0;
				}

				&:last-child {
					border-top-left-radius: 0;
					border-top-right-radius: 0;
				}
			}

			+ span {
				padding-top: #{(($box-selector--height - $box-selector--line-height) / 2) - 1px};
				padding-bottom: #{(($box-selector--height - $box-selector--line-height) / 2) - 1px};
			}

			&:checked {

				+ span {

					&:after {
						top: -1px;
						right: -1px;
					}
				}
			}
		}

		&.sui-disabled {
			pointer-events: none;
			position: relative;
			background-color: transparent;
			box-shadow: none;

			input {

				+ span {
					border-color: palette(silver, medium);

					.sui-tag-pro,
					.sui-tag-beta {
						position: absolute;
						top: 5px;
						right: 5px;
					}

					~ span {
						border-color: palette(silver, medium);
						border-top-style: dashed;
						border-top-color: palette(gray, lighter);
					}
				}
			}

			&:hover {
				box-shadow: none;
			}
		}
	}
}